import React, {Component} from "react";

export class PatientInfo extends Component {
    componentWillMount() {
        this.state = {
            hospital: this.props.hospital, //
            patientList: this.props.patientList, //
        }
    }

    componentWillReceiveProps(nextProps, nextState) {
        /*
         * render
         * */
        if (nextProps.hospital !== nextState.hospital || nextProps.patientList !== nextState.patientList) {
            this.setState({
                hospital: nextProps.hospital, //
                patientList: nextProps.patientList, //
            })
        }
    }

    render() {
        let {hospital, patientList} = this.state;
        return (
            <table className="patInfo">
                <tbody>
                <tr>
                    <td colSpan="4">
                        <h1>
                            {hospital}
                        </h1>
                    </td>
                </tr>
                <tr>
                    <td colSpan="4"><h3></h3></td>
                </tr>
                <tr className="percent4">
                    <td><span className="btitle"></span><span className="bline">{patientList.patientName}</span></td>
                    <td><span className="btitle"></span><span className="bline">{patientList.sex}</span></td>
                    <td><span className="btitle"></span><span className="bline">{patientList.age}</span></td>
                    <td><span className="btitle"></span><span
                        className="bline">{patientList.admissionDateTime}</span></td>
                </tr>
                <tr className="percent4">
                    <td><span className="btitle"></span><span className="bline">{patientList.endemicName}</span></td>
                    <td><span className="btitle"></span><span className="bline">{patientList.bedNo}</span></td>
                    <td><span className="btitle"></span><span className="bline">{patientList.inpNo}</span></td>
                    <td><span className="btitle"></span><span className="bline">{patientList.medicalHao}</span></td>
                </tr>
                </tbody>
            </table>
        )
    }
}



