import React, {Component} from "react";
import {getNewDate} from "./tempChart";

export class TableInfo extends Component {
    componentWillMount() {
        this.state = {
            beginDate: this.props.beginDate, //
            dayOps: this.props.dayOps, ///
            dayList: this.props.dayList //
        }
    }

    componentWillReceiveProps(nextProps, nextState) {
        /*
         * beginDaterender
         * */
        if (nextProps.beginDate !== nextState.beginDate) {
            this.setState({
                beginDate: nextProps.beginDate, //
                dayOps: nextProps.dayOps, ///
                dayList: nextProps.dayList //
            })
        }
    }

    /*
     * @method getDaysArray 
     * @param {String} beginDate 
     * */
    getDaysArray = (beginDate) => {
        const showDays = 7;
        //
        let daysArray = [];
        for (let i = 0; i < showDays; i++) {
            let newDate = getNewDate(beginDate, i);
            daysArray.push(newDate);
        }
        return daysArray;
    }
    /*
     * @method getTimes  2-6-10-14-18-22
     * */
    getTimes = () => {
        //timesArray
        let timesArray = [];
        for (let i = 0; i < 42; i++) {
            let num = 2 + (i % 6) * 4;
            let td = <td key={i}>{num}</td>;
            if (num === 22) {
                td = <td key={i} className="borderR">{num}</td>;
            }
            timesArray.push(td);
        }
        return timesArray;
    }

    render() {
        let {beginDate, dayOps, dayList} = this.state;
        let daysArray = this.getDaysArray(beginDate);
        let timesArray = this.getTimes();
        return (
            <tbody>
            <tr id="dateTR">
                <td></td>
                {daysArray.map((v, i) => {
                    return <td key={i} colSpan={6} className="borderR">{v}</td>
                })}
                <td/>
            </tr>
            <tr id="hospDaysTr">
                <td></td>
                {daysArray.map((v, i) => {
                    return <td key={i} colSpan={6} className="borderR">{dayList[i]}</td>
                })}
                <td/>
            </tr>
            <tr id="operaDaysTr">
                <td></td>
                {daysArray.map((v, i) => {
                    return <td key={i} colSpan={6} className="borderR">{dayOps[i]}</td>
                })}
                <td/>
            </tr>
            <tr id="timeTr">
                <td></td>
                {timesArray.map((v, i) => {
                    return v
                })}
                <td/>
            </tr>
            </tbody>
        )
    }
}



