import React, {Component} from "react";
import moment from "moment";
import {Button, DatePicker, message, Spin} from "antd";
import {getNewDate} from "./tempChart";
import {GridTable} from "./gridTable";
import {Colgroup} from "./colgroup";
import {PatientInfo} from "./patientInfo";
import {TableInfo} from "./tableInfo";
import {SvgCenter} from "./svgCenter";

const ButtonGroup = Button.Group;

export class TemperatureChart extends Component {
    componentWillMount() {
        this.state = {
            beginDate: this.props.beginDate, //
            hospital: this.props.hospital, //
            patientList: this.props.patientList, //
            dayOps: this.props.dayOps, ///
            breathingList: this.props.breathingList, //
            dayList: this.props.dayList, //
            dayMap: this.props.dayMap, //
            pointTime: this.props.pointTime, // 
            loading: this.props.loading,
            PageCount: this.props.PageCount,
            PageIndex: this.props.PageIndex
        }
    }

    componentWillReceiveProps(nextProps, nextState) {
        /*
         * render
         */
        this.setState({
            beginDate: nextProps.beginDate,
            hospital: nextProps.hospital,
            patientList: nextProps.patientList,
            dayOps: nextProps.dayOps,
            breathingList: nextProps.breathingList,
            dayList: nextProps.dayList,
            dayMap: nextProps.dayMap,
            pointTime: nextProps.pointTime,
            loading: nextProps.loading,
            PageCount: nextProps.PageCount,
            PageIndex: nextProps.PageIndex
        })
      
    }

    /**
     * 
     * @param curDate 
     * @param disDays Number"first""last"
     */
    changeBeginDay = (PageIndex, IsLast7Days,flag) => {
        if(flag == 'last' || flag == 'forward'){
            if((this.state.PageIndex  == this.state.PageCount)){
                message.info("");
                return;
            }
        }
        if(flag == 'first' || flag == 'back'){
            if(this.state.PageIndex == 1){
                message.info("");
                return;
            }
        }
        switch(flag){
            case 'forward':
                ++PageIndex
                break;
            case 'back':
                --PageIndex;
                break;
        }
        this.props.changeBeginDay(PageIndex,IsLast7Days);
    }
    /**
     * 
     * @param date
     * @param dateString
     */
    onDateChange = (date, dateString) => {
        if (dateString !== "") {
            console.log("onDateChange")
            this.props.changeBeginDay(dateString);
        }
    }
    /**
     *   - 
     * @param currentDate
     */
    disabledDate = (currentDate) => {
        let curDate = new Date(moment(currentDate).format("YYYY-MM-DD"));
        let admissionDateTime = this.state.patientList.admissionDateTime; //
        let dischargeDateTime = this.state.patientList.dischargeDateTime || moment(new Date()).format("YYYY-MM-DD");//
        let admissionDay = new Date(moment(admissionDateTime).format("YYYY-MM-DD"));
        let dischargeDay = new Date(moment(dischargeDateTime).format("YYYY-MM-DD"));
        return curDate < admissionDay || curDate > dischargeDay;
    }

    render() {
        let {beginDate, patientList, hospital, dayOps, dayList, pointTime, breathingList, dayMap, loading,PageCount,
       PageIndex} = this.state;
       let pageNum = {  
           float:"right"
       };
        return (
            <div className="temperature-chart" style={{width: '791px'}}>
                <Spin spinning={loading}>
                    <div className="footBtn">
                    {/* <DatePicker
              size="small"
              value={moment(beginDate)}
              format="YYYY-MM-DD"
              onChange={this.onDateChange}
              disabledDate={this.disabledDate}
            /> */}
            <ButtonGroup>
              <Button type="primary" size="small"
onClick={this.changeBeginDay.bind(this, 1, 0,'first')}></Button>
<Button type="primary" size="small" onClick={this.changeBeginDay.bind(this, PageIndex, 0,'back')}></Button>
<Button type="primary" size="small" onClick={this.changeBeginDay.bind(this, PageIndex, 0,'forward')}></Button>
<Button type="primary" size="small"
onClick={this.changeBeginDay.bind(this, PageCount, 0,'last')}></Button>
</ButtonGroup>
<span style={pageNum}>{PageIndex}</span>
</div>
<div className="tempCartSvg">
    <div className="fixed_div">
        <PatientInfo hospital={hospital} patientList={patientList}/>
    </div>
    <div className="flow_div">
        <table className="showInfo">
            <Colgroup/>
            <TableInfo beginDate={beginDate} dayOps={dayOps} dayList={dayList}/>
        </table>
        <div className="svgCenter">
            <SvgCenter data={pointTime} curDate={beginDate}/>
        </div>
        <table className="tableSvg">
            <Colgroup/>
            <GridTable breathingList={breathingList} dayMap={dayMap}/>
        </table>
    </div>
</div>
</Spin>
</div>
        )
}
}



